.glimpses-container{
    
    min-height: 100vh;
    margin: 10vh;
    padding-top: 5vh;
    box-sizing: border-box;
}
.glimpses-header{
    padding-top: 20px;
    padding-left: 150px;
    padding-right: 150px;
    margin: 10px;
    justify-content: space-between;
    margin: 0 0 40px 0; 
    display: -webkit-box; 
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify; 
    -webkit-justify-content: space-between;
    align-items: flex-end;
}
.glimpses-btn{
padding-top: 25px;
padding-right: 125px;
display: flex;
flex: 50px;
}
.button {
    background-color: black;
    color: white;
    padding: 20px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
    width: 190px;
    height: 60px;

  }
.glimpses-btn-seeall{
    border-radius: 28px;
}

